import React from 'react';
import Link from 'next/link';

interface LayoutProps {
  children: React.ReactNode;
}

const Layout: React.FC<LayoutProps> = ({ children }) => {
  return (
    <div className="min-h-screen flex flex-col bg-[#0A192F] text-white">
      <nav className="p-4 flex items-center justify-between">
        <div className="flex items-center">
          <Link href="/" className="text-xl font-bold">
            aCloudBridge
          </Link>
        </div>
        <div className="flex items-center space-x-4">
          <Link href="/" className="hover:text-[#64FFDA]">
            Home
          </Link>
          <Link href="/contact" className="hover:text-[#64FFDA]">
            About Me
          </Link>
          <Link href="/signup" className="hover:text-[#64FFDA]">
            Register
          </Link>
          <Link href="/login" className="hover:text-[#64FFDA]">
            Login
          </Link>
        </div>
      </nav>
      <main className="flex-grow">{children}</main>
      <footer className="bg-[#112240] text-gray-300 text-center py-4">
        <p>&copy; {new Date().getFullYear()} aCloudBridge. All rights reserved.</p>
      </footer>
    </div>
  );
};

export default Layout;